@import "../../node_modules/bootstrap/scss/bootstrap.scss";
@import url("https://fonts.googleapis.com/css2?family=Courgette&family=Handlee&family=Titan+One&display=swap");

$reddy: rgb(207, 4, 4);
$lightReddy: rgb(251, 147, 147);

body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  font-family: Handlee;
  font-size: 1.25rem;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

img {
  max-width: 400px;
  border: none;
  border-radius: 8px;
  box-shadow: 0 0.5rem 1rem rgb(0 0 0 / 30%);
}

/* =================================================================
          NAVBAR
  ================================================================ */

.navbar-brand {
  img {
    width: 125px;
    height: auto;
  }
}

/* =================================================================
          HEADER
  ================================================================ */

#header {
  background-color: #000;
  height: fit-content;

  #logo {
    padding-block-start: 5rem;
    padding-inline: 2rem;
  }

  section:nth-of-type(2) {
    padding-block-end: 4rem;
  }

  section {
    p {
      font-family: "Titan One", Courgette;
      font-size: 7.5vw;

      @media screen and (min-width: 626px) {
        font-size: 6.2vw;
      }
    }
  }
}

/* =================================================================
SECTIONS
================================================================ */

main {
  flex-grow: 1;
  margin-block-end: 5rem;

  section {
    padding-block-start: 2.5rem;
  }
}

.intro {
  a {
    width: 200px;
    height: 65px;
    text-decoration: none;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
      transform: scale(1.05);
    }
  }
}

section {
  margin-block-end: 2.5rem;
  padding-inline: 1.5rem;
  max-width: 900px;
  margin: auto;

  .section-title {
    text-align: center;
    margin-block: 2.5rem;
    font-weight: bold;
  }

  .section-sub-title {
    font-size: calc(1.2rem + 0.5vw);
    text-align: center;
    margin-block-end: 1rem;
    color: rgb(220, 53, 69);
  }

  .about-text {
    margin-block-end: 1.5rem;
    line-height: 1.65;
  }
}

#services {
  .row:nth-of-type(1) {
    margin-block-end: 10%;
  }

  .row:first-child {
    align-self: center;
  }

  .image {
    margin: auto;
  }
}

.services-list {
  margin-block-end: 1.5rem;
  align-self: center;

  & div {
    font-size: 1.1rem;
    padding: 0.375rem;
  }
}

/* =================================================================
          REVIEWS
  ================================================================ */

#reviewCarousel {
  height: 225px;
}

.review {
  .user-date-box {
    display: flex;
    justify-content: space-between;
    margin-block-end: 0.5rem;
  }

  .username-rating {
    display: flex;
    align-items: center;
    line-height: 1.1;

    img {
      margin-inline-end: 0.5rem;
      border-radius: 50%;
    }
  }

  .username {
    font-weight: 600;
  }

  .rating {
    letter-spacing: -1px;
    color: rgb(92, 92, 92);
    opacity: 0.7;
  }

  .review-date,
  .review-text {
    font-size: 1rem;
  }

  .review-text {
    margin-top: 1rem;
  }
}

/* =================================================================
          CONTACT
  ================================================================ */

form {
  border: 1.25px solid rgb(142, 142, 142);
  border-radius: 5px;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: auto;

  .form-control,
  .form-select {
    border: 1.25px solid rgb(142, 142, 142);
  }

  .invalid-feedback,
  .valid-feedback {
    margin-block-end: 0;
  }

  div {
    margin-block-end: 0.5rem;

    .form-label {
      margin: 0;
    }
  }

  button {
    align-self: flex-end;
  }
}

/* =================================================================
          FOOTER
  ================================================================ */

footer {
  display: flex;
  flex-direction: column;
  font-size: 1rem;

  .contact-info {
    margin-block: 1rem;
    text-align: center;

    .contact-title {
      color: $lightReddy;
      font-weight: bold;
      margin-block-end: 0.375rem;
      font-size: 1.15rem;
    }

    .tel-number {
      display: block;
      text-decoration: none;
      color: #fff;
    }

    div {
      width: fit-content;
      margin: 1.25rem auto;
    }
  }

  .tel,
  .address,
  .hours {
    svg,
    h5 {
      display: inline-block;
      font-size: 1.25rem;
    }

    svg {
      vertical-align: top;
      margin-inline-end: 0.375rem;
      color: $lightReddy;
      width: 20px;
      height: 20px;
    }
  }

  .hours-list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  small {
    font-size: 0.75rem;
  }

  .social-container {
    margin: 0;
    .icon-links {
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 15px;

      img {
        width: 35px;
        height: 35px;
      }
    }
  }
}

/* =================================================================
          ANIMATION
  ================================================================ */

.start,
.middle {
  margin-block-end: 0.875rem;
}

.start {
  animation-duration: 3s;
  animation-name: enter-right;
}

@keyframes enter-right {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }

  to {
    transform: translateX(0%);
    opacity: 1;
  }
}

.middle {
  animation-duration: 3.5s;
  animation-name: fadeIn;
}

@keyframes skew {
  from {
    transform: skew(60deg);
  }

  85% {
    transform: skew(60deg);
    opacity: 0;
  }

  to {
    transform: skew(0deg);
  }
}

@keyframes fadeIn {
  from {
    transform: skew(60deg);
    transform: translateY(140px);
    opacity: 0;
  }

  85% {
    opacity: 0.05;
  }

  to {
    transform: translateY(0%);
    transform: skew(0deg);
    opacity: 1;
  }
}

.end {
  animation-duration: 4s;
  animation-name: enter-left;
}

@keyframes enter-left {
  from {
    transform: translateX(100%);
    opacity: 0;
  }

  75% {
    transform: translateX(100%);
    opacity: 0;
  }

  to {
    transform: translateX(0%);
    opacity: 1;
  }
}

.intro a {
  animation-duration: 1s;
  animation-name: heartbeat;
  animation-iteration-count: infinite;
}

@keyframes heartbeat {
  from {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  to {
    transform: scale(1);
    border: none;
  }
}
